export default {
	data() {
		return {
			news: []
		}
	},
	methods:{
		openinfo(e){
			// console.log('id->',e.currentTarget.dataset.newsid);
			var newsid = e.currentTarget.dataset.newsid;
			uni.navigateTo({
				url: '../info/info?newsid='+newsid
			});
		}
	},
	onLoad() {
		uni.request({
			url: 'https://unidemo.dcloud.net.cn/api/news',
			method: 'GET',
			data: {},
			success: res => {
				// console.log(res);
				this.news = res.data;
			},
			fail: () => {},
			complete: () => {}
		});
	}
}
